.btn-holder{
    width: 200px;
    margin: 0 auto;
    position: relative;
}
.button {
    position: relative;
    background: rgb(252, 0, 168);
    margin : 20px auto;
    width : 200px;
    height : 50px;
    overflow: hidden;
    text-align : center;
    -webkit-transition : .2s;
    transition : .2s;
    cursor : pointer;
    border-radius: 3px;
    box-shadow: 0px 1px 2px rgba(0,0,0,.2);
}
.btnTwo {
    position : relative;
    width : 200px;
    height : 100px;
    margin-top: -100px;
    padding-top: 2px;
    background : rgb(38, 166, 117);
    left : -250px;
    -webkit-transition : .3s;
    transition : .3s;
}
.btnText {
    color : white;
    -webkit-transition : .3s;
    transition : .3s;
}
.btnText2 {
    margin-top : 17%;
    margin-right : -130px;
    color : #FFF;
}
.button:hover .btnTwo{ /*When hovering over .button change .btnTwo*/
    left: -130px;
}
.button:hover .btnText{ /*When hovering over .button change .btnText*/
    margin-left : 65px;
}
.button:active { /*Clicked and held*/
    box-shadow: 0px 5px 6px rgba(0,0,0,0.3);
}
.home{
    position: relative;
    display: grid;
    height: 95vh;
    grid-template-columns: 10% 40% 40% 10%; 
    grid-template-rows: 1fr 6fr; 

    grid-template-areas:
    " . t t . "
    " . d s . ";
    grid-gap: 20px;
    text-align: center;
}

.title{
    grid-area: t
}

#drawing {
    grid-area: d
}

#story {
    grid-area: s
}

